import { AppState } from 'react-native';

class AppStateListener {
  constructor(foregroundHandler) {
    AppState.addEventListener('change', this.handleAppStateChange.bind(this));
    this.state = AppState.currentState;
    this.foregroundHandler = foregroundHandler;
  }

  handleAppStateChange(newState) {
    if (this.state.match(/inactive|background/) && newState === 'active') {
      this.foregroundHandler && this.foregroundHandler();
    }
    this.state = newState;
  }
}

export default AppStateListener;



