import { AsyncStorage } from 'react-native';

let cache = {};

const readyPromise = AsyncStorage.getAllKeys()
  .then((keys = []) => {
    return AsyncStorage.multiGet(keys);
  })
  .then((stores = []) => {
    stores.forEach(([key, value]) => {
      cache[key] = value;
    });
    return Promise.resolve();
  });

class RoxCache {
  set(key, value) {
    cache[key] = value;
    return AsyncStorage.setItem(key, value);
  }

  get(key) {
    return cache[key];
  }

  getReadyPromise() {
    return readyPromise;
  }
}

export default new RoxCache();



