import crypto from 'browserify-sign';
import publicKey from '../assets/publicKey.pem';

const DEFAULT_HASH_FUNC = 'RSA-SHA256';
const DEFAULT_SIGNATURE_ENCODING = 'base64';
const DEFAULT_PAYLOAD_ENCODING = 'utf-8';

class RoxCrypto {
  static verify(payload, signature = '', hash_algo = DEFAULT_HASH_FUNC) {
    if (payload.constructor !== String) {
      payload = JSON.stringify(payload);
    }
    const verifier = crypto.createVerify(hash_algo);
    verifier.update(payload, DEFAULT_PAYLOAD_ENCODING);
    const result = verifier.verify(publicKey, signature, DEFAULT_SIGNATURE_ENCODING);
    return result;
  }
}

export default RoxCrypto;



