import Variant from './Variant';
import Flag from './Flag';

class RNEntitiesProvider {
  createFlag(defaultValue) {
    return new Flag(defaultValue);
  }

  createVariant(defaultValue, options) {
    return new Variant(defaultValue, options);
  }
}

export default new RNEntitiesProvider();



