export const freezeOptions = {
  freezeOptionUntilLaunch: 1,
  freezeOptionUntilForeground: 2,
  freezeOptionNone: 3
};

let _defaultFreezeOptions = null;
export function setDefaultFreezeOption(freezeOption) {
  if (isFreezeOptionValid(freezeOption)) {
    _defaultFreezeOptions = freezeOption;
  } else {
    throw new Error(`Invalid freeze option: ${freezeOption}`);
  }
}

export function getDefaultFreezeOption() {
  return _defaultFreezeOptions;
}

export function isFreezeOptionValid(freezeOption) {
  return Object.keys(freezeOptions).find(function(key) {
    return freezeOptions[key] === freezeOption;
  });
}

export const defaultFreezeOptionForPlatform = freezeOptions.freezeOptionUntilForeground;



